<script>
import Vue from "vue";
import SortedTablePlugin from "vue-sorted-table";
import functions from "../../../util/functions.js";
import constants from "../../../util/constants";
import { mapState } from "vuex";

Vue.use(SortedTablePlugin, {
  ascIcon: '<i class="mdi mdi-arrow-down"></i>',
  descIcon: '<i class="mdi mdi-arrow-up"></i>',
});

export default {
  data: function () {
    return {
      constants,

      is_doing_action: false,
      action_message: "En cours",

      sortBy: "Nom & prénom",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      is_loading_articles: true,
      fields: [
        { key: "n", sortable: false },
        //{ key: "action", sortable: true },
        { key: "date", sortable: true },
        { key: "type_alerte", sortable: true },
        { key: "coordonnes", sortable: true },
        //{ key: "localisation", sortable: true },
        { key: "observation", sortable: true },
        // { key: "prix_promotionnel", sortable: true },
        // { key: "est_disponible", sortable: true },
        // { key: "est_un_nouvel_article", sortable: true },
        // { key: "description", sortable: true }, 
        // { key: "collection", sortable: true }, 
      ],
      all_articles: [],
      list_categories_articles: [],
      search_text: "",
    };
  },
  async created() {
    this.is_loading_articles = true;
    let url = this.constants.api_url + "/get_my_alerts/";
    let response = await fetch(url, {
      method: "post",
      headers  : {
        "Content-Type" : "application/json"
      },
      body : JSON.stringify({
        user_id : this.id_utilisateur,
        user_lat : -1,
        user_long : -1, 
      }),
      cache: "no-cache",
      //credentials: "include",
    });
    let data = await response.json();
    if (data.Alerts) {
      let listTypes = [ {libelle : "Alerte Police", id : 1}, 
        {libelle : "Police Incendie" , id : 2}, 
        {libelle : "Service Social", id : 3},
        {libelle : "Croix rouge", id : 4},
      ]
      let pos = 0;
      this.all_articles = data.Alerts.map((article) => {
        pos++;
        let n = pos;
        let type_alerte = listTypes.find(tt => tt.id == article.service_id)
        if(type_alerte)
          type_alerte = type_alerte.libelle
        else
          type_alerte = article.service_id
        
        return {
            ...article,
            n,
            date : functions.obtenirDateEtHeureEnFrancais(article.created_at),
            type_alerte : type_alerte,
            coordonnes : '( ' + article.latitude + ',' +  article.longitude + " )",
            observation : "",
            localisation : article.Ville + ', ' + article.Commune + ', ' + article.Quartier ,

            // prix_unitaire : article.prix_unitaire + " $",
            // est_disponible : article.est_disponible == 1 ? 'Oui' : 'Non',
            // est_en_promotion : article.est_en_promotion == 1 ? 'Oui' : "Non",
            // collection : article.libelle_collection,
            // est_un_nouvel_article : article.est_nouveau == 1 ? "Oui": 'Non',
            action: article.id,
          };
      });
    } else {
      this.$swal({
        type: "error",
        title: "Une erreur est survenue",
        text: "Status : " + data.status + "\nMessage : " + data.message,
        //footer: '<a href>Why do I have this issue?</a>'
      });
    }
    this.is_loading_articles = false;
  },
  computed: {
    ...mapState([
      "is_connected",
      "nom_utilisateur",
      "id_utilisateur",
      "est_admin",
    ]),
    rows() {
      return this.all_articles.length;
    },
    articles() {
      return this.all_articles.filter((article) => {
        if (this.search_text == "") {
          return this.all_articles;
        }
        let attrs = ["date", "type_alerte", "observation"];
        for (let attr of attrs) {
          if (
            article[attr] != undefined &&
            article[attr]
              .toLowerCase()
              .toLowerCase()
              .includes(this.search_text.toLowerCase())
          ) {
            return true;
          }
        }
      });
    },
  },
  methods: {
    ...functions,
    async doDeleteFaq(id_alerte) {
      let res = await this.$swal({
        title: "Supprimer ?",
        text: "Voulez-vous vraiment supprimer cet alerte ?",
        type: "question",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Supprimer",
        cancelButtonText: "Annuler",
      });
      if (!res.value) {
        return;
      }
      this.action_message = "Suppression  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/alert/" + id_alerte;
      let response = null;
      try {
        response = await fetch(url, {
          method: "delete",
          cache: "no-cache",
          body: JSON.stringify(this.article),
          credentials: "include",
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        this.all_articles = this.all_articles.filter((article) => article.id != id_alerte);
        this.$swal({
          type: "success",
          title: "Effectué",
          text: "La article a été supprimée avec succès. ",
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }  else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
  },
};
</script>

<template>
  <section class="mt-5">
    <div class="page-header">
      <h3 class="page-title">Mes Alertes</h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <a href="javascript:void(0);">Alertes</a>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            Mes Alertes
          </li>
        </ol>
      </nav>
    </div>
    <div v-if="!is_loading_articles">
      <div v-if="is_doing_action">
        <div class="card mb-4">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ action_message }}</p>
          </div>
        </div>
      </div>
      <div class=" ">
        <div class="card">
          <div class="card-body">
            <!-- <h4 class="card-title mb-5">Liste de Faqs</h4> -->
            <div class="row mb-4 d-flex justify-content-start">
              <div class="col-md-7 justify-content-start">
                <button
                  type="button"
                  @click="$router.push('/admin/alertes/nouveau')"
                  :disabled="is_doing_action"
                  class="btn-icon-text btn btn-primary mb-3 mb-md-0"
                >
                  <i class="fa fa-plus btn-icon-prepend"></i>
                  Nouvelle Alerte
                </button>
              </div>
              <div class="col-md-5 d-none">
                <b-form-group label="" label-for="input5">
                  <b-form-input
                    type="text"
                    v-model="search_text"
                    placeholder="Rechercher"
                  ></b-form-input>
                </b-form-group>
              </div>
            </div>

            <div class="table-responsive">
              <b-table
                :items="articles"
                id="table-list"
                responsive
                :per-page="perPage"
                :current-page="currentPage"
                :fields="fields"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
              >
                <template v-slot:cell(status)="data">
                  <span v-html="data.value"></span>
                </template>
                <template v-slot:cell(action)="data">
                  <div class="d-flex justify-content-center">
                    <button
                      class="btn btn-sm d-none btn-outline-primary py-1 mr-1"
                      title="Modifier ce article"
                      @click="
                        () => $router.push('/admin/articles/editer/' + data.value)
                      "
                      :disabled="is_doing_action"
                    >
                      <i class="fa fa-pencil"></i>
                    </button>
                    <button
                      @click="doDeleteFaq(data.value)"
                      class="btn btn-sm btn-outline-danger py-1"
                      title="Supprimer ce article"
                      :disabled="is_doing_action"
                    >
                      <i class="fa fa-trash"></i>
                    </button>
                  </div>
                </template>
                <template v-slot:cell(profil)="data">
                  <span v-html="data.value"></span>
                </template>
              </b-table>
              <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                :per-page="perPage"
                aria-controls="table-list"
                align="right"
              >
              </b-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mt-5 pt-4" v-else>
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement des données en cours</p>
        </div>
      </div>
    </div>
    <div></div>
  </section>
</template>
